.drawer {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 80%;
    max-width: 300px;
    padding: 20px 10px;
    box-sizing: border-box;
    background: rgb(179, 209, 226);
    transform: translateX(0px);
    transition: transform .32s ease-in;
    z-index: 90;
}

.close {
    transform: translateX(-300px);
}

.drawer ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.drawer ul li {
    margin-bottom: 15px;
}

.drawer ul li a{
color: #363f54;
font-size: 30px;
text-decoration: none;
background-color: white;
position: relative;
padding:  0 20px 10px 20px;
transition: opacity .3s;
}

.drawer ul li a:hover{
opacity: .7;
}