.input {
    margin-bottom: 15px;

}

.input label {
    margin-bottom: 3px;
    padding: 0;
    display: block;
    font-weight: bold;
}

.input input {
    display: block;
    box-sizing: border-box;
    border: 1px solid #bebebe;
    padding: 7px;
    margin: 0 0 5px;
    width: 100%;
    outline: none;
    transition: all 300ms ease-in-out;
}

.input span {
color: #f01f30;
font-size: 12px;
font-weight: bold;
}

.invalid {
    color: #f01f30;

}