
.finishedQuiz {
    padding: 20px;
    color: white;
    border: 2px solid white;
    border-radius: 5px;
    box-sizing: border-box;
    margin: 0 10px;
  }
  
  .finishedQuiz ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .finishedQuiz ul li  i {
    margin-left: 10px;
  }
  

  .successFa {
    color: rgba(161, 240, 69, 0.7);
}

.errorFa {
    color: rgba(211, 45, 45, 0.7);
}  