.button {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 4px;
    border: 1px solid #ccc;
    color: #000;
    margin-right: 15px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;

}

.button:focus {
    outline: none;

}

.button:hover {
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, 0.2);
}

.button:active {
    box-shadow: inset 2px 2px 1px rgba(0, 0, 0, 0.5);
    
}

.button:disabled {
    background: #ccc;
    color: #000;
    cursor: not-allowed;
}



.errorBtn {
    background: rgba(240, 87, 108, 0.7);

}


.successBtn {
background: rgba(161, 240, 69, 0.7);
}

.primary {
    background: #2884f6;
    color: #fff;
}
